html,
body,
#root
{
    
    position: fixed;
    top: 0px;
    left: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    /*background-image: url("./background.png");*/
    background-color:black;
    background-size: 100%;
    /*background: radial-gradient(at 50% 100%, #2727c3 40%, #06068f 80%, #020263 100%);*/
}
.bg {
  position: absolute;
  background: radial-gradient(at 50% 100%, #873740 0%, #272730 40%, #171720 80%, #070710 100%);
}

/* fonts.css */
@font-face {
  font-family: 'Sprayerz';
  src: local('Sprayerz'), url('./fonts/Sprayerz.ttf') format('truetype');
  /* You may add more font properties like font-weight, font-style if needed */
}


h1,h2,h3,h4,h5 {
  font-family: 'Italiana', serif;
  color: #fff;
  margin-bottom: 32px !important;
}

h1 {
  font-size: 60px !important;
  color: white;
  font-family: 'Sprayerz';
  letter-spacing: 5px;

  animation-name: example;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}

h2 {
  font-size: 40px !important;
  color: white;
}

a {
  list-style: none;
  color:white;
  font-weight: 300;
  
  text-decoration: none;
}

.right{
  float:right;
}

.navbar {
  display: flex;
  justify-content:right;
  align-items: center;
  padding: 30px;
  width:100%;
  position:fixed;
  z-index:20;
}

canvas{
  touch-action: auto!important;
}
.nav-links {
  font-family: 'Roboto', sans-serif;
  list-style: none;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 8%;
  transform: scaleX(1.4);
  font-size: 18px;
  line-height: 1.8em;

}

.nav-links li {
  margin-left: 25%;
  
}

.nav-links a {
  color: #fff;
  font-size: 17px;
  text-decoration: none;
  

}

.nav-links a:hover {
  color: #a4a4d1;
  
}

.nav-links a.active::after {
  text-decoration: dotted;
  text-decoration: wavy;
  color: #0000ff;
}

.left-text {

  font-family: 'Roboto', sans-serif;
  position: absolute; /* Position the element absolutely within its containing parent */
  left: 10%; /* Adjust the left distance from the left edge of the screen */
  padding:3%; /* Adjust the top distance from the top edge of the screen */
  padding-top:190px;
  font-size: 10px; /* Adjust the font size as needed */
  color: #ffffff; /* Text color */
  font-weight: lighter; /* Make the text bold */
  transform: scaleX(1.5);
  opacity:80%;
  line-height: 1.3;
}

.logo-spcwby{
  
  list-style: none;
  color:white;
  text-decoration: none;
  font-family: 'Sprayerz', sans-serif;
  font-size: 38px;
  left: 10%; 
  position: absolute; /* Position the element absolutely within its containing parent */
  padding-top: 30px;
  
}


.low-opacity {
  color: white;
  opacity: 40%;
}

.line{
  font-family: 'Roboto', sans-serif;
  position: relative; /* Position the element absolutely within its containing parent */
  top:22px;
  left:-10px;
  font-size: 10px; 
  color: #ffffff; 
  font-weight: bold;
  transform: scaleY(5);
  opacity:80%;
}

.photo {
  display: inline-block;
  margin: 20px;
  border: 1px solid #ccc;
  padding: 10px;
}

/* Style the images */
.photo img {
  max-width: 100%;
  height: auto;
}
